.navBar {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  background-color: #fff;
}
.nav_Link {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
h1 {
  font-size: 16px;
  font-weight: 800;
}
p {
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.material-symbols-outlined {
  margin-right: 5px;
  font-size: 16px;
}
